export default {
  name: "StickerItem",
  props: {
    todo: {
      type: Object
    }
  },

  data() {
    return {
      parseTodo: JSON.parse(JSON.stringify(this.todo)),
      smallFont: false,
      date: new Date()
    };
  },

  computed: {
    formatUsername: {
      get() {
        if (!this.parseTodo.username) return "";
        return this.parseTodo.username.slice(0, 1).toUpperCase() + this.parseTodo.username.slice(1);
      },

      set(val) {
        this.parseTodo.username = val;
      }

    },

    formatDate() {
      const [week, month, day] = ("" + this.date).split(" ");
      return week + " " + month + " " + day;
    }

  },
  watch: {
    parseTodo: {
      handler(newVal) {
        newVal.username.length > 5 ? this.smallFont = true : this.smallFont = false;
        if (newVal.username.length > 10) newVal.username = newVal.username.slice(0, 10);
      },

      deep: true
    },
    todo: {
      handler(newVal) {
        this.parseTodo = JSON.parse(JSON.stringify(newVal));
      },

      deep: true
    }
  }
};